import React, { useEffect, useState } from "react";
import wrapper from "../../commons/WebComponentWrapper";
import { EventAPI } from "../../commons/EventAPI";
import {decompressFromBase64} from "lz-string";

const dispatchedEvents = [];

const PagePreview = (props, ref) => {
    const [previewcontents, setPreviewcontents] = useState([]);
    const [previewcontext, setPreviewcontext] = useState({});
    const [previewfunctions, setPreviewfunctions] = useState({});

    useEffect(() => {
        let previewData = localStorage.getItem("srsPageData");
        previewData = decompressFromBase64(previewData);
        if(previewData) {
            previewData = JSON.parse(previewData);
            setPreviewcontents(previewData.contents);
            setPreviewcontext(previewData.context);
            setPreviewfunctions(previewData.functions);
        }else {
            setPreviewcontents([]);
        }
    }, []);

    return (<srs-widget ts={Date.now() + ""} styles={JSON.stringify({minHeight: "fit-content", maxHeight: "unset", margin: "5px", overflow: "auto"})} 
        ref={(el) => {
            if (!el) return;
            el.elementid = "preview_1";
            el.widgetid = "preview_1";
            el.widgetcontents = JSON.stringify(previewcontents);
            el.widgetcontext = JSON.stringify(previewcontext);
            el.widgetfunctions = JSON.stringify(previewfunctions);
            el.theme = props.theme;
        }} 
        name="preview_1" />);
};



const styles = `
  /* Custom styles for the Accordion component */
`;

const props = [
];

const slots = [];

const WrappedComponent = wrapper(
    React.forwardRef(PagePreview),
    props,
    "mui",
    ["srs-widget"]
  );
export { WrappedComponent, styles, props, dispatchedEvents, slots };
